body {
  font-family: sans-serif;
}

button {
  margin: 0;
  overflow: visible;
  text-transform: none;
  letter-spacing: 0.01em;
  display: inline-block;
  line-height: normal;
  white-space: nowrap;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
  -webkit-user-drag: none;
  user-select: none;
  box-sizing: border-box;
  font-size: 100%;
  padding: 0.5em 1em;
  border: transparent;
  text-decoration: none;
  border-radius: 2px;
  background-color: #0078e7;
  color: #fff;
  font-family: inherit;
}
